import { Field, InputType } from '@nestjs/graphql';
import { PartialType } from '@nestjs/mapped-types';
import { Type } from 'class-transformer';
import { IsArray, IsNotEmpty, IsOptional, IsString, IsUrl, IsUUID, MinLength, ValidateNested } from 'class-validator';
import { RelationExists } from '../../../../validation/custom/relation-exists/relation-exists.decorator';
import { PaymentGateway } from '../../../../orm-config/entity/payment/payment-gateway.entity';
import { ColumnExists } from '../../../../validation/custom/column-exists/column-exists.decorator';
import { PaymentGatewayPlanInput } from '../gateway-plan/payment-gateway-plan.input';
import { CreatePaymentGatewayInput } from './create-payment-gateway.input';
import { IsUniqeArrayObjets } from "../../../../validation";

@InputType()
export class UpdatePaymentGatewayInput extends PartialType(CreatePaymentGatewayInput) {
  
  @Field(() => String)
  @IsUUID(4)
  @RelationExists(PaymentGateway)
  id: string;

  @Field(() => String, { nullable: true })
  @IsNotEmpty()
  @IsString()
  @MinLength(5)
  @ColumnExists(PaymentGateway)
  name: string;

  @Field(() => String, { nullable: true })
  @IsNotEmpty()
  @IsString()
  @IsUrl()
  @ColumnExists(PaymentGateway)
  apiSandboxTokenUrl: string;

  @Field(() => String, { nullable: true })
  @IsNotEmpty()
  @IsString()
  @IsUrl()
  @ColumnExists(PaymentGateway)
  apiSandboxPaymentUrl: string;

  @Field(() => String, { nullable: true })
  @IsNotEmpty()
  @IsString()
  @IsUrl()
  @ColumnExists(PaymentGateway)
  apiTokenUrl: string;

  @Field(() => String, { nullable: true })
  @IsNotEmpty()
  @IsString()
  @IsUrl()
  @ColumnExists(PaymentGateway)
  apiPaymentUrl: string;

  @Field(() => [PaymentGatewayPlanInput], { nullable: true })
  @ValidateNested({ each: true })
  @IsUniqeArrayObjets('paymentPlan.id')
  @IsArray()
  @IsOptional()
  @Type(() => PaymentGatewayPlanInput)
  paymentGatewayPlans: PaymentGatewayPlanInput[];
}
